body{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    cursor: crosshair;
  }

.menu_conteiner {
    display: grid;
    grid-template: "menu_art menu_frontend" 50vh
                   "menu_head menu_contact" 50vh;
}

.hide{
  opacity: 0;
  visibility: hidden;
  transition: all 300ms;
}

@media only screen and (max-width: 600px){

  .menu_conteiner {
    display: grid;
    grid-template: "menu_art" 50vh
                   "menu_frontend" 50vh
                   "menu_head" 50vh
                   "menu_contact" 50vh;
}

}


